import * as actionTypes from '../actionTypes/toolbar';

const initialState = {
  selectedButton: null,
  toolbarOptions: [],
  showLogonButton: true,
};

const setSelectedButton = (state, action) => ({
  ...state,
  selectedButton: action.string,
});

const setToolbarOptions = (state, action) => ({
  ...state,
  toolbarOptions: action.array,
})

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_SELECTED_BUTTON:
      return setSelectedButton(state, action);
    case actionTypes.SET_TOOLBAR_OPTIONS:
      return setToolbarOptions(state, action);
    default:
      return state;
  }
};

export default reducer;