import React from 'react';
import {
  makeStyles,
  Typography,
  CircularProgress,
} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  holding: {
    position: "fixed",
    left: 0,
    top: "40vh",
    width: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column"
  },
  spinner: {
    marginBottom: theme.spacing(3)
  },
}));

const GettingResultsSpinner = () => {
  const classes = useStyles();
  return (
    <div className={classes.holding}>
      <CircularProgress className={classes.spinner} size={100} />
      <Typography>Getting search results...</Typography>
    </div>
  );
}

export default GettingResultsSpinner;