import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import uuidv1 from "uuid/v1";
import { SUBMIT_REQUEST } from "../actionTypes/api";
import { getBaseUrl, getSelectedSong } from "./selectors";
import {
  setSendingRequest,
  setSelectedSong,
  setNotificationMessage,
  setNotificationOpen,
  setSuccessfulRequestDetails
} from "../actions/index";
import { SUCCESS_PATH } from "../../constants/index";

function* submitRequest(action) {
  const { singer, history } = action;
  const baseUrl = yield select(getBaseUrl);
  const songObj = yield select(getSelectedSong);

  yield put(setSendingRequest(true));
  const body = {
    ...songObj,
    RequestID: uuidv1(),
    Singer: singer,
    State: "pending",
    CompletedDateTime: 0
  };

  try {
    yield call(axios.post, `${baseUrl}/new-request`, body);
    yield put(setSelectedSong(null));
    yield put(setSuccessfulRequestDetails(body));

    // bit of a hack to stop the redirect catching.
    try {
      yield call(history.push(SUCCESS_PATH));
    } catch (e) {
      // do nothing
    }

  } catch (error) {
    yield put(
      setNotificationMessage(`Error submitting request, please try again`)
    );
    yield put(setNotificationOpen(true));
  }

  yield put(setSendingRequest(false));
}

function* watcherSaga() {
  yield takeLatest(SUBMIT_REQUEST, submitRequest);
}

export default watcherSaga;
