import { connect } from 'react-redux';
import App from './App';
import { setToolbarOptions } from './store/actions'

const mapStateToProps = state => {
  const { admin } = state;
  const { djModeActive } = admin;
  return {
    djModeActive,
  }
}

const mapDispatchToProps = dispatch => ({
  setToolbarOptions: array => dispatch(setToolbarOptions(array))
});

export default connect(mapStateToProps, mapDispatchToProps)(App);