import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { COMPLETE_REQUEST } from '../actionTypes/api';
import { getBaseUrl, getToken, getSongRequests, getCompletedSongRequests } from "./selectors";
import {
  setNotificationOpen,
  setNotificationMessage,
  setSongRequests,
  setCompletedSongRequests,
  setSelectedSong,
} from "../actions/index";

function* completeRequest(action) {
  const { songObj } = action;
  const baseUrl = yield select(getBaseUrl);
  const token = yield select(getToken);
  const songRequests = yield select(getSongRequests);
  const completedRequests = yield select(getCompletedSongRequests);

  const body = {
    ...songObj,
    State: "completed",
    CompletedDateTime: new Date().toISOString(),
  }

  delete body.Position;

  const { RequestID } = songObj;

  try {
    yield call(axios.post, `${baseUrl}/completed-request`, body, { headers: { "X-Auth-Token": token } });
    yield put(setSongRequests(
      songRequests.filter(obj => obj.RequestID !== RequestID).map(
        (obj, i) => ({
          ...obj,
          Position: i + 1,
        })
      )
    ));
    yield put(setCompletedSongRequests([body, ...completedRequests]));

    yield put(setSelectedSong(null));
  } catch (error) {
    yield put(setNotificationMessage(`Error completing request: ${error}`));
    yield put(setNotificationOpen(true));
  }
}

function* watcherSaga() {
  yield takeLatest(COMPLETE_REQUEST, completeRequest);
}

export default watcherSaga;
