import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { SUBMIT_SONG_ISSUE } from '../actionTypes/api';
import { getBaseUrl, getToken, getSelectedSong } from "./selectors";
import {
  setNotificationOpen,
  setNotificationMessage,
  setSelectedSong,
} from "../actions/index";

function* submitSongIssue(action) {
  const { issue } = action;
  const baseUrl = yield select(getBaseUrl);
  const token = yield select(getToken);
  const songData = yield select(getSelectedSong);

  yield put(setSelectedSong(null));

  const body = {
    task: "recordFileIssue",
    issue,
    songData,
  }

  try {
    const response = yield call(axios.post, `${baseUrl}/admin-task`, body, { headers: { "X-Auth-Token": token } });
    const { data } = response;
    const { message } = data;
    yield put(setNotificationMessage(`Success: ${message}`));
    yield put(setNotificationOpen(true));
  } catch (error) {
    yield put(setNotificationMessage(`Error submitting issue: ${error}`));
    yield put(setNotificationOpen(true));
  }
}

function* watcherSaga() {
  yield takeLatest(SUBMIT_SONG_ISSUE, submitSongIssue);
}

export default watcherSaga;