import { connect } from "react-redux";
import Search from "./Search";
import { setSelectedButton, searchForSongs } from "../../store/actions/index";
import {
  BROWSE_BY_ARTIST_PATH,
  BROWSE_BY_TITLE_PATH,
  REQUESTS_PATH
} from "../../constants/index";

const mapStateToProps = state => {
  const { admin } = state;
  const { djModeActive } = admin;
  return {
    swipeLeftPath: BROWSE_BY_ARTIST_PATH,
    swipeRightPath: djModeActive ? REQUESTS_PATH : BROWSE_BY_TITLE_PATH
  };
};

const mapDispatchToProps = dispatch => ({
  setSelectedButton: string => dispatch(setSelectedButton(string)),
  search: (string, searchBy) =>
    dispatch(searchForSongs(string, searchBy, false))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Search);
