import * as actionTypes from '../actionTypes/browseCache';
import { ARTIST, TITLE } from '../../constants/index';

const initialState = {
  artist: {},
  title: {},
};

const addToArtistCache = (state, action) => {
  const { id, array } = action;
  const newArtist = {
    ...state.artist,
    [id]: array,
  };

  return {
    ...state,
    artist: newArtist
  }
}

const addToTitleCache = (state, action) => {
  const { id, array } = action;
  const newTitle = {
    ...state.artist,
    [id]: array,
  };

  return {
    ...state,
    title: newTitle
  }
}

const addToBrowseCache = (state, action) => {
  const { searchBy } = action;

  if (searchBy === ARTIST) {
    return addToArtistCache(state, action);
  }

  if (searchBy === TITLE) {
    return addToTitleCache(state, action);
  }
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.ADD_TO_BROWSE_CACHE:
      return addToBrowseCache(state, action);
    default:
      return state;
  }
};

export default reducer;