import { spawn } from "redux-saga/effects";

import apiPollForRequestsSaga from "./apiPollForRequests";
import apiPollForSubmittedRequestsSaga from "./apiPollForSubmittedRequests";

import apiGetRequestsSaga from "./apiGetRequests";
import apiAuthenticationSaga from "./apiAuthentication";
import apiSubmitRequestSaga from "./apiSubmitRequest";
import apiSearchForSongsSaga from "./apiSearchForSongs";
import apiGetSubmittedRequests from './apiGetSubmittedRequests';
import apiCompleteRequestSaga from './apiCompleteRequest';
import apiClearRequestsSaga from './apiClearRequests';
import apiReloadDatabaseSaga from './apiReloadDatabase';
import apiSubmitSongIssueSaga from './apiSubmitSongIssue';

export default function* rootSaga() {
  yield spawn(apiPollForRequestsSaga);
  yield spawn(apiPollForSubmittedRequestsSaga);
  yield spawn(apiGetRequestsSaga);
  yield spawn(apiAuthenticationSaga);
  yield spawn(apiSubmitRequestSaga);
  yield spawn(apiSearchForSongsSaga);
  yield spawn(apiGetSubmittedRequests);
  yield spawn(apiCompleteRequestSaga);
  yield spawn(apiClearRequestsSaga);
  yield spawn(apiReloadDatabaseSaga);
  yield spawn(apiSubmitSongIssueSaga);
}
