import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { SEARCH_FOR_SONGS } from "../actionTypes/api";
import { getBaseUrl, getArtistBrowseCache, getTitleBrowseCache } from "./selectors";
import {
  setGettingSearchResults,
  setNotificationMessage,
  setNotificationOpen,
  setSearchResults,
  addToBrowseCache,
  setCurrentSearch,
  clearCurrentSearch,
} from "../actions/index";
import { TITLE, ARTIST } from "../../constants/index";

const processSearchResults = (data, searchBy) => {
  let sortKey1 = ARTIST;
  let sortKey2 = TITLE;
  if (searchBy === TITLE) {
    sortKey1 = TITLE;
    sortKey2 = ARTIST;
  }
  return data.sort((a, b) => {
    if (a[sortKey1] < b[sortKey1]) {
      return -1;
    }

    if (a[sortKey1] > b[sortKey1]) {
      return 1;
    }

    // If it's got this far the now sort by the second key
    if (a[sortKey2] < b[sortKey2]) {
      return -1;
    }

    if (a[sortKey2] > b[sortKey2]) {
      return 1;
    }

    return 0;
  });
};

function* searchForSongs(action) {
  const { string, searchBy, browse } = action;
  const baseUrl = yield select(getBaseUrl);
  let cache = {};
  let loadedFromCache = false;

  yield put(setGettingSearchResults(true));
  yield put(setCurrentSearch(string, searchBy, browse));

  // check if these results have been cached
  if (browse) {
    if (searchBy === ARTIST) {
      cache = yield select(getArtistBrowseCache);
    }

    if (searchBy === TITLE) {
      cache = yield select(getTitleBrowseCache);
    }

    const cachedResults = cache[string] || [];

    if (cachedResults && cachedResults.length > 0) {
      yield put(setSearchResults(cache[string]));
      loadedFromCache = true;

      // close the spinner and continue in the background
      yield put(setGettingSearchResults(false));
    }
  }

  // create the query
  const query = `?value=${encodeURI(
    string
  )}&searchby=${searchBy.toLowerCase()}&browse=${browse}`;

  try {
    const response = yield call(axios.get, `${baseUrl}/find-songs${query}`);
    const { data } = response;
    const sortedResults = processSearchResults(data, searchBy);

    if (!loadedFromCache) {
      yield put(setSearchResults(sortedResults));
      yield put(setGettingSearchResults(false));
    }

    if (browse) {
      yield put(addToBrowseCache(string, sortedResults, searchBy));
    }

  } catch (error) {
    if (!loadedFromCache) {
      yield put(
        setNotificationMessage(`Error whist searching, please try again`)
      );
      yield put(setNotificationOpen(true));
    }
    yield put(setGettingSearchResults(false));
    yield put(clearCurrentSearch());
  }
}

function* watcherSaga() {
  yield takeLatest(SEARCH_FOR_SONGS, searchForSongs);
}

export default watcherSaga;
