import React from "react";
import { makeStyles, ListItem, Typography, IconButton } from "@material-ui/core";
import { CheckCircleOutline, FileCopy } from '@material-ui/icons';

const useStyles = makeStyles(theme => ({
  listItem: {
    width: "100%",
    boxShadow:
      "0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12)",
    backgroundColor: "rgba(255,255,255,0.03)",
    marginBottom: theme.spacing(1)
  },
  paper: {
    width: "100%",
    display: 'flex',
    flexDirection: 'row',
  },
  root: {
    flexGrow: 1
  },
  requestContainer: {
    display: "flex",
    flexDirection: "row",
    alignItems: "baseline"
  },
  dataContainer: {
    flexGrow: 1
  },
  dateTime: {
    marginRight: theme.spacing(4),
    color: theme.palette.action.disabled
  },
  songSummaryContainer: {
    display: "flex",
    flexDirection: "column",
    flexGrow: 1
  },
  row: {
    display: "flex",
    flexDirection: "row"
  },
  halfRowContainer: {
    width: "50%",
    minWidth: "50%",
    maxWidth: "50%",
    display: "flex",
    flexDirection: "row",
    alignItems: "baseline"
  },
  right: {
    justifyContent: "flex-end"
  },
  keyFormatting: {
    width: "55px",
    minWidth: "55px",
    maxWidth: "55px",
    color: theme.palette.action.disabled
  },
  buttonContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  hidden: {
    display: 'none',
  }
}));

const KaraokeSongSummaryListItem = props => {
  const { songObj, setSelectedSong, completeRequest } = props;
  const classes = useStyles();

  const {
    Position,
    Artist,
    Title,
    DiscRef,
    Key,
    Length,
    Singer,
    DateTime,
    CompletedDateTime,
  } = songObj;

  const timeString = dtObj => {
    return dtObj.toLocaleString().match(/[0-9]{1,2}:[0-9]{1,2}:[0-9]{1,2}/g);
  };


  const handleCopyRef = (e, str) => {
    const el = document.createElement('textarea');
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    e.stopPropagation();
  }

  return (
    <ListItem
      className={classes.listItem}
      button
      onClick={() => setSelectedSong(songObj)}
    >
      <div className={classes.paper}>
        <div className={classes.dataContainer}>
          {Singer ? (
            <div id="request-container" className={classes.requestContainer}>
              {Position ? (
                <Typography
                  variant="h5"
                  className={classes.dateTime}
                >{`${Position}.`}</Typography>
              ) : null}
              <Typography variant="h5" className={classes.dateTime}>
                {timeString(CompletedDateTime || DateTime)}
              </Typography>
              <Typography variant="h5">{Singer}</Typography>
            </div>
          ) : null}
          <div
            id="song-summary-container"
            className={classes.songSummaryContainer}
          >
            <div id="top-row" className={classes.row}>
              <div id="title-container" className={classes.halfRowContainer}>
                <Typography
                  className={classes.keyFormatting}
                  variant="subtitle2"
                  color="inherit"
                >
                  Title:
              </Typography>
                <Typography variant="subtitle1" noWrap>
                  {Title}
                </Typography>
              </div>
              <div id="artist-container" className={classes.halfRowContainer}>
                <Typography
                  className={classes.keyFormatting}
                  variant="subtitle2"
                  color="inherit"
                >
                  Artist:
              </Typography>
                <Typography variant="subtitle1" noWrap>
                  {Artist}
                </Typography>
              </div>
            </div>
            <div id="bottom-row" className={classes.row}>
              <div id="ref-container" className={classes.halfRowContainer}>
                <Typography
                  className={classes.keyFormatting}
                  variant="subtitle2"
                  color="inherit"
                >
                  Ref:
              </Typography>
                <Typography variant="subtitle1" noWrap>
                  {DiscRef}
                </Typography>
              </div>
              <div className={classes.halfRowContainer}>
                <div id="key-container" className={classes.halfRowContainer}>
                  <Typography
                    className={classes.keyFormatting}
                    variant="subtitle2"
                    color="inherit"
                  >
                    Key:
                </Typography>
                  <Typography variant="subtitle1" noWrap>
                    {Key}
                  </Typography>
                </div>
                <div
                  id="length-container"
                  className={[classes.halfRowContainer, classes.right].join(" ")}
                >
                  <Typography
                    className={classes.keyFormatting}
                    variant="subtitle2"
                    color="inherit"
                  >
                    Length:
                </Typography>
                  <Typography variant="subtitle1" noWrap>
                    {Length}
                  </Typography>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.buttonContainer}>
          {Singer && !CompletedDateTime ? (
            <div>
              <IconButton onClick={e => handleCopyRef(e, DiscRef)}>
                <FileCopy fontSize="large" />
              </IconButton>
              <IconButton
                onClick={e => {
                  e.stopPropagation();
                  completeRequest(songObj)
                }}>
                <CheckCircleOutline fontSize="large" />
              </IconButton>
            </div>
          ) : null}
        </div>
      </div>
    </ListItem>
  );
};

export default KaraokeSongSummaryListItem;
