import React from "react";
import { withRouter } from "react-router-dom";
import { makeStyles, Typography, LinearProgress, Box } from "@material-ui/core";
import { useSwipeable } from "react-swipeable";
import { TOOLBAR_HEIGHT } from "../../constants/index";

import logo from "../../assets/images/logo-karaoke-png.png";

const useStyles = makeStyles(theme => ({
  success: {
    minHeight: `calc(100vh - ${TOOLBAR_HEIGHT}px - ${theme.spacing(3)}px)`
  },
  img: {
    maxWidth: "100%"
  },
  centerContent: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    marginBottom: theme.spacing(4)
  },
  message: {
    width: "60vw",
    minWidth: "460px"
  },
  box: {
    marginRight: theme.spacing(1)
  },
  boldBox: {
    marginRight: theme.spacing(1),
    color: theme.palette.highlightColor
  },
  countdown: {
    marginTop: theme.spacing(2)
  }
}));

const Success = props => {
  const { history, successfulRequestDetails } = props;
  const classes = useStyles();
  const { Singer, Title, Artist } = successfulRequestDetails || {};

  const handlers = useSwipeable({
    onSwipedLeft: () => history.push("/"),
    onSwipedRight: () => history.push("/"),
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  const [completed, setCompleted] = React.useState(0);

  React.useEffect(() => {
    function progress() {
      setCompleted(oldCompleted => {
        if (oldCompleted === 100) {
          history.push("/");
        }
        return Math.min(oldCompleted + 1, 100);
      });
    }

    const timer = setInterval(progress, 100);
    return () => {
      clearInterval(timer);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div id="success" className={classes.success} {...handlers}>
      <div className={classes.centerContent}>
        <img src={logo} alt="logo" className={classes.img} />
      </div>
      <div className={classes.centerContent}>
        <div className={classes.message}>
          <Typography paragraph component="div">
            <Box className={classes.box} display="inline">
              Thank you
            </Box>
            <Box
              className={classes.boldBox}
              display="inline"
              fontWeight="fontWeightBold"
            >
              {Singer}
            </Box>
            <Box className={classes.box} display="inline">
              for your request of
            </Box>
            <Box
              className={classes.boldBox}
              display="inline"
              fontWeight="fontWeightBold"
            >
              {Title}
            </Box>
            <Box className={classes.box} display="inline">
              in the style of
            </Box>
            <Box
              className={classes.boldBox}
              display="inline"
              fontWeight="fontWeightBold"
            >
              {Artist}.
            </Box>
            <Box className={classes.box} display="inline">
              It has been submitted to the DJ.
            </Box>
          </Typography>
          <Typography paragraph>
            Keep listening and you will be called when it's your turn to sing.
          </Typography>
          <Typography paragraph>
            While you're waiting why not request another?
          </Typography>
          <div>
            <Typography variant="caption" align="center" gutterBottom>
              Please note the DJ will not be able to tell you how long until
              you are called. Please be patient.
            </Typography>
            <LinearProgress
              className={classes.countdown}
              variant="determinate"
              value={completed}
              color="secondary"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Success);
