import { delay, call, take, put, race } from 'redux-saga/effects';
import { getRequests } from '../actions';
import { START_POLL_FOR_REQUESTS, STOP_POLL_FOR_REQUESTS } from '../actionTypes/api'

function* pollForRequests() {
  while (true) {
    try {
      yield put(getRequests());
      yield delay(60000);
    } catch (err) {
      yield delay(10000);
    }
  }
}

function* watcherSaga() {
  while (true) {
    yield take(START_POLL_FOR_REQUESTS);
    yield race([call(pollForRequests), take(STOP_POLL_FOR_REQUESTS)]);
  }
}

export default watcherSaga;
