import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { GET_SUBMITTED_REQUESTS } from "../actionTypes/api";
import { getBaseUrl } from "./selectors";
import { setTickerString } from "../actions/index";

const createString = data => {
  const strings = data.map(obj => `${obj.Title} in the style of ${obj.Artist}`);

  // randomize order
  let currentIndex = strings.length;
  let temporaryValue;
  let randomIndex;

  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = strings[currentIndex];
    strings[currentIndex] = strings[randomIndex];
    strings[randomIndex] = temporaryValue;
  }

  return strings.join(" -- ");
};

function* getRequests() {
  const baseUrl = yield select(getBaseUrl);

  try {
    const response = yield call(axios.get, `${baseUrl}/submitted-requests`);
    const { data } = response;
    yield put(setTickerString(data.length < 3 ? "" : createString(data)));
  } catch (error) {
    console.log(error);
  }
}

function* watcherSaga() {
  yield takeLatest(GET_SUBMITTED_REQUESTS, getRequests);
}

export default watcherSaga;
