import * as actionTypes from "../actionTypes/admin";

const initialState = {
  logonDialogOpen: false,
  djModeActive: false,
  baseUrl: null,
  notificationOpen: false,
  notificationMessage: null,
  token: null
};

const showLogonDialog = (state, action) => ({
  ...state,
  logonDialogOpen: action.bool
});

const setDjModeActive = (state, action) => ({
  ...state,
  djModeActive: action.bool
});

const setBaseUrl = (state, action) => ({
  ...state,
  baseUrl: action.url
});

const setNotificationOpen = (state, action) => ({
  ...state,
  notificationOpen: action.bool
});

const setNotificationMessage = (state, action) => ({
  ...state,
  notificationMessage: action.string
});

const setToken = (state, action) => ({
  ...state,
  token: action.token
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SHOW_LOGON_DIALOG:
      return showLogonDialog(state, action);
    case actionTypes.SET_DJ_MODE_ACTIVE:
      return setDjModeActive(state, action);
    case actionTypes.SET_BASE_URL:
      return setBaseUrl(state, action);
    case actionTypes.SET_NOTIFICATION_OPEN:
      return setNotificationOpen(state, action);
    case actionTypes.SET_NOTIFICATION_MESSAGE:
      return setNotificationMessage(state, action);
    case actionTypes.SET_TOKEN:
      return setToken(state, action);
    default:
      return state;
  }
};

export default reducer;
