import * as actionTypes from '../actionTypes/displayProperties';

export const initialState = {
  height: window.innerHeight,
  width: window.innerWidth,
  isLandscape: window.innerHeight >= window.innerWidth,
};

const setWindowHeightWidth = (state, action) => {
  const { height, width } = action;
  return {
    ...state,
    height,
    width,
  };
};

const setIsLandscape = (state, action) => ({
  ...state,
  isLandscape: action.bool,
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_WINDOW_HEIGHT_WIDTH:
      return setWindowHeightWidth(state, action);
    case actionTypes.SET_IS_LANDSCAPE:
      return setIsLandscape(state, action);
    default:
      return state;
  }
};

export default reducer;
