import { connect } from "react-redux";
import AppListeners from "./AppListeners";
import {
  setWindowHeightWidth,
  setIsLandscape,
  setBaseUrl,
  startPollForTicker,
  stopPollForTicker
} from "../../store/actions/index";

export const mapDispatchToProps = dispatch => ({
  setWindowHeightWidth: (height, width) => {
    dispatch(setWindowHeightWidth(height, width));
    dispatch(setIsLandscape(height >= width));
  },
  setBaseUrl: url => dispatch(setBaseUrl(url)),
  startPollForTicker: () => dispatch(startPollForTicker()),
  stopPollForTicker: () => dispatch(stopPollForTicker())
});

export default connect(
  null,
  mapDispatchToProps
)(AppListeners);
