import { connect } from "react-redux";
import Notification from "./Notification";
import { setNotificationOpen } from "../../store/actions/index";

const mapStateToProps = state => {
  const { admin } = state;
  const { notificationOpen, notificationMessage } = admin;

  return {
    notificationOpen,
    notificationMessage
  };
};

const mapDispatchToProps = dispatch => ({
  closeNotification: () => dispatch(setNotificationOpen(false))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Notification);
