import React from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  makeStyles,
  FormControl,
} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200
  }
}));

const LogonDialog = props => {
  const { logonDialogOpen, handleClose, djModeActive, handleSubmit } = props;
  const classes = useStyles();

  const [password, setPassword] = React.useState("");

  React.useEffect(() => {
    setPassword('');
  }, [logonDialogOpen])

  const handleKeypress = e => {
    const { key, target } = e;
    const { value } = target;
    if (key === 'Enter') {
      handleSubmit(djModeActive, password);
    }
    setPassword(value)
  }

  return (
    <Dialog
      open={logonDialogOpen}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{`${
        djModeActive ? "Log out from" : " Login to"
        } DJ mode`}</DialogTitle>
      <DialogContent>
        {!djModeActive ? (
          <FormControl>
            <TextField
              value={password}
              onChange={handleKeypress}
              id="standard-password-input"
              label="Password"
              className={classes.textField}
              type="password"
              autoComplete="current-password"
              margin="normal"
              autoFocus
              onKeyPress={handleKeypress}
            />
          </FormControl>
        ) : null}
      </DialogContent>
      <DialogActions>
        <Button
          onClick={() => handleSubmit(djModeActive, password)}
          disabled={!djModeActive && password.length === 0}
        >
          {djModeActive ? "Log out" : "Login"}
        </Button>
        <Button onClick={handleClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
};

export default LogonDialog;
