import { connect } from 'react-redux';
import ToolbarButton from './ToolbarButton';
import { BUTTON_LABEL_INDEX } from '../../constants/index';

const mapStateToProps = (state, ownProps) => {
  const { toolbar } = state;
  const { selectedButton } = toolbar;
  const { buttonType } = ownProps;

  return {
    label: BUTTON_LABEL_INDEX[buttonType],
    selected: selectedButton === buttonType,
  }
}

export default connect(mapStateToProps)(ToolbarButton);