import React, { Suspense } from "react";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import { createMuiTheme, CssBaseline } from "@material-ui/core";
import { ThemeProvider } from "@material-ui/styles";

import AppListeners from "./components/AppListeners/AppListenersConnector";
import Home from "./containers/Home/HomeConnector";
import Layout from "./containers/Layout/Layout";
import Search from "./containers/Search/SearchConnector";
import BrowseBy from "./containers/BrowseBy/BrowseByConnector";
import Results from "./containers/Results/ResultsConnector";
import Success from "./containers/Success/SuccessConnector";

import {
  SEARCH,
  BROWSE_BY_ARTIST,
  BROWSE_BY_TITLE,
  REQUESTS,
  ADMIN,
  SEARCH_PATH,
  BROWSE_BY_ARTIST_PATH,
  BROWSE_BY_TITLE_PATH,
  RESULTS_PATH,
  REQUESTS_PATH,
  ADMIN_PATH,
  SUCCESS_PATH,
  ARTIST,
  TITLE,
} from "./constants/index";

const Requests = React.lazy(() =>
  import("./containers/Requests/RequestsConnector")
);

const Admin = React.lazy(() =>
  import("./containers/Admin/AdminConnector")
);


const App = props => {
  const { setToolbarOptions, djModeActive } = props;

  const theme = createMuiTheme({
    palette: {
      type: "dark",
      primary: {
        main: "#00FF00"
      },
    },
    overrides: {
      MuiAppBar: {
        colorPrimary: {
          backgroundColor: "#292929"
        }
      },
    }
  });

  let routes = (
    <Switch>
      <Route path={SEARCH_PATH} exact component={Search} />
      <Route
        path={BROWSE_BY_ARTIST_PATH}
        exact
        render={() => <BrowseBy variant={BROWSE_BY_ARTIST} searchBy={ARTIST} />}
      />
      <Route
        path={BROWSE_BY_TITLE_PATH}
        exact
        render={() => <BrowseBy variant={BROWSE_BY_TITLE} searchBy={TITLE} />}
      />
      <Route path={RESULTS_PATH} exact component={Results} />
      <Route path={SUCCESS_PATH} exact component={Success} />
      <Route path="/" component={Home} />
    </Switch>
  );

  const toolbarOptions = [
    {
      buttonType: SEARCH,
      path: SEARCH_PATH
    },
    {
      buttonType: BROWSE_BY_ARTIST,
      path: BROWSE_BY_ARTIST_PATH
    },
    {
      buttonType: BROWSE_BY_TITLE,
      path: BROWSE_BY_TITLE_PATH
    }
  ];

  if (djModeActive) {
    routes = (
      <Switch>
        <Route path={SEARCH_PATH} exact component={Search} />
        <Route
          path={BROWSE_BY_ARTIST_PATH}
          exact
          render={() => (
            <BrowseBy variant={BROWSE_BY_ARTIST} searchBy={ARTIST} />
          )}
        />
        <Route
          path={BROWSE_BY_TITLE_PATH}
          exact
          render={() => <BrowseBy variant={BROWSE_BY_TITLE} searchBy={TITLE} />}
        />
        <Route path={RESULTS_PATH} exact component={Results} />
        <Route path={REQUESTS_PATH} exact component={Requests} />
        <Route path={SUCCESS_PATH} exact component={Success} />
        <Route path={ADMIN_PATH} exact component={Admin} />
        <Route path="/" component={Home} />
      </Switch>
    );

    toolbarOptions.unshift({
      buttonType: REQUESTS,
      path: REQUESTS_PATH
    });

    toolbarOptions.push({
      buttonType: ADMIN,
      path: ADMIN_PATH
    });
  }

  setToolbarOptions(toolbarOptions);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppListeners />
      <Router>
        <Suspense fallback={<div>Loading...</div>}>
          <Layout>{routes}</Layout>
        </Suspense>
      </Router>
    </ThemeProvider>
  );
};

export default App;
