import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { getBaseUrl, getToken } from "./selectors";
import {
  HANDLE_LOGON_REQUEST,
  HANDLE_LOGOFF_REQUEST
} from "../actionTypes/admin";
import {
  showLogonDialog,
  setDjModeActive,
  setNotificationOpen,
  setNotificationMessage,
  setToken
} from "../actions/index";

function* requestLogon(action) {
  const { credentials } = action;
  const baseUrl = yield select(getBaseUrl);
  try {
    const response = yield call(axios.post, `${baseUrl}/admin-task`, {
      task: "login",
      value: credentials
    });

    const { data } = response;
    const { token } = data;

    yield put(setToken(token));
    yield put(setDjModeActive(true));
  } catch (error) {
    yield put(setNotificationMessage(`Error logging on: ${error}`));
    yield put(setNotificationOpen(true));
  }

  yield put(showLogonDialog(false));
}

function* requestLogoff() {
  const baseUrl = yield select(getBaseUrl);
  const token = yield select(getToken);

  try {
    yield call(
      axios.post,
      `${baseUrl}/admin-task`,
      {
        task: "logout"
      },
      { headers: { "X-Auth-Token": token } }
    );

    yield put(setToken(""));
    yield put(setDjModeActive(false));
  } catch (error) {
    yield put(setNotificationMessage(`Error logging off: ${error}`));
    yield put(setNotificationOpen(true));
  }

  yield put(showLogonDialog(false));
}

function* watcherSaga() {
  yield takeLatest(HANDLE_LOGON_REQUEST, requestLogon);
  yield takeLatest(HANDLE_LOGOFF_REQUEST, requestLogoff);
}

export default watcherSaga;
