import React from 'react';
import { makeStyles } from "@material-ui/core/styles";
import { withRouter } from 'react-router-dom';
import singers from '../../assets/images/singers.png';
import { SEARCH } from '../../constants/index'

import logo from "../../assets/images/logo-karaoke-png.png";

const useStyles = makeStyles(theme => ({
    home: {
        textAlign: 'center',
        color: 'white',
        textDecoration: 'none',
        width: '100%',
        height: '100%',
        left: 0,
        top: 0,
    },
    centerContent: {
        width: "100%",
        display: "flex",
        justifyContent: "center",
        flexWrap: "wrap",
        marginBottom: theme.spacing(4)
    },
    img: {
        maxWidth: '100%',
        height: 'auto',
        width: 'auto',
    }
}));

const Home = props => {
    const { history, setSelectedButton } = props;
    const classes = useStyles();

    const handleClick = () => {
        setSelectedButton(SEARCH);
        history.push('/search');
    }

    return (
        <div role="presentation" className={classes.home} onClick={handleClick}>
            <div className={classes.centerContent}>
                <img src={logo} alt="logo" className={classes.img} />
            </div>
            <h1>Digital Song Book</h1>
            <h3>Tap to start</h3>
            <div>
                <img className={classes.img} src={singers} alt="karaoke book" />
            </div>
        </div>
    )
};

export default withRouter(Home);