import * as actionTypes from '../actionTypes/displayProperties';

// eslint-disable-next-line import/prefer-default-export
export const setWindowHeightWidth = (height, width) => ({
  type: actionTypes.SET_WINDOW_HEIGHT_WIDTH,
  height,
  width,
});


export const setIsLandscape = bool => ({
  type: actionTypes.SET_IS_LANDSCAPE,
  bool,
});