import * as actionTypes from "../actionTypes/api";

const initialState = {
  gettingSearchResults: false,
  sendingRequest: false,
  pollStatus: '',
};

const setSendingRequest = (state, action) => ({
  ...state,
  sendingRequest: action.bool
});

const gettingSearchResults = (state, action) => ({
  ...state,
  gettingSearchResults: action.bool
});

const setPollForRequestStatus = (state, action) => ({
  ...state,
  pollStatus: action.string,
})

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_SENDING_REQUEST:
      return setSendingRequest(state, action);
    case actionTypes.SET_GETTING_SEARCH_RESULTS:
      return gettingSearchResults(state, action);
    case actionTypes.SET_POLL_FOR_REQUEST_STATUS:
      return setPollForRequestStatus(state, action);
    default:
      return state;
  }
};

export default reducer;
