import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { CLEAR_REQUESTS } from '../actionTypes/api';
import { getBaseUrl, getToken } from "./selectors";
import {
  setNotificationOpen,
  setNotificationMessage,
  setSongRequests,
  setCompletedSongRequests,
} from "../actions/index";

function* clearRequests() {
  const baseUrl = yield select(getBaseUrl);
  const token = yield select(getToken);

  const body = {
    task: 'clearRequests'
  }

  try {
    const response = yield call(axios.post, `${baseUrl}/admin-task`, body, { headers: { "X-Auth-Token": token } });
    const { data } = response;
    const { message } = data;
    yield put(setNotificationMessage(`Success: ${message}`));
    yield put(setNotificationOpen(true));
    yield (put(setSongRequests([])));
    yield (put(setCompletedSongRequests([])));
  } catch (error) {
    yield put(setNotificationMessage(`Error clearing requests: ${error}`));
    yield put(setNotificationOpen(true));
  }
}

function* watcherSaga() {
  yield takeLatest(CLEAR_REQUESTS, clearRequests);
}

export default watcherSaga;