/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";

const AppListeners = props => {
  const {
    setWindowHeightWidth,
    setBaseUrl,
    startPollForTicker,
    stopPollForTicker
  } = props;

  const handleWindowResize = () => {
    const { innerHeight, innerWidth } = window;
    setWindowHeightWidth(innerHeight, innerWidth);
  };

  React.useEffect(() => {
    window.addEventListener("resize", handleWindowResize);

    let url = window.location.href.match(/^http:\/\/(.*):/g); // http://{{ some ip address/ dns name / localhost }}:
    const port = 8000;

    setBaseUrl(url + port + "/api");
    startPollForTicker();

    return () => {
      window.removeEventListener("resize", handleWindowResize);
      stopPollForTicker();
    };
  }, []);

  return null;
};

export default AppListeners;
