import * as actionTypes from "../actionTypes/results";

const initialState = {
  searchResults: [],
  selectedSong: null,
  songRequests: [],
  completedSongRequests: [],
  tickerString: "",
  successfulRequestDetails: null,
  currentSearch: '',
};

const clearSearchResults = state => ({
  ...state,
  searchResults: []
});

const setSearchResults = (state, action) => ({
  ...state,
  searchResults: action.array
});

const clearSelectedSong = state => ({
  ...state,
  selectedSong: null
});

const setSelectedSong = (state, action) => ({
  ...state,
  selectedSong: action.obj
});

const setSongRequests = (state, action) => ({
  ...state,
  songRequests: action.array
});

const setCompletedSongRequests = (state, action) => ({
  ...state,
  completedSongRequests: action.array
});

const setTickerString = (state, action) => ({
  ...state,
  tickerString: action.string
});

const setSuccessfulRequestDetails = (state, action) => ({
  ...state,
  successfulRequestDetails: action.obj
});

const setCurrentSearch = (state, action) => {
  const { string, searchBy, browse } = action;

  return {
    ...state,
    currentSearch: `Showing results for ${searchBy === 'Both' ? 'Artist & Title' : searchBy} ${browse ? 'beginning with' : 'containing'} "${string}".`,
  }
}

const clearCurrentSearch = (state, action) => ({
  ...state,
  currentSearch: '',
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.CLEAR_SEARCH_RESULTS:
      return clearSearchResults(state);
    case actionTypes.SET_SEARCH_RESULTS:
      return setSearchResults(state, action);
    case actionTypes.CLEAR_SELECTED_SONG:
      return clearSelectedSong(state);
    case actionTypes.SET_SELECTED_SONG:
      return setSelectedSong(state, action);
    case actionTypes.SET_SONG_REQUESTS:
      return setSongRequests(state, action);
    case actionTypes.SET_COMPLETED_SONG_REQUESTS:
      return setCompletedSongRequests(state, action);
    case actionTypes.SET_TICKER_STRING:
      return setTickerString(state, action);
    case actionTypes.SET_SUCCESSFUL_REQUEST_DETAILS:
      return setSuccessfulRequestDetails(state, action);
    case actionTypes.SET_CURRENT_SEARCH:
      return setCurrentSearch(state, action);
    case actionTypes.CLEAR_CURRENT_SEARCH:
      return clearCurrentSearch(state, action);
    default:
      return state;
  }
};

export default reducer;
