import { connect } from "react-redux";
import BrowseBy from "./BrowseBy";
import { setSelectedButton, searchForSongs } from "../../store/actions/index";
import {
  BROWSE_BY_TITLE,
  BROWSE_BY_ARTIST_PATH,
  BROWSE_BY_TITLE_PATH,
  SEARCH_PATH,
  ADMIN_PATH,
} from "../../constants/index";

const mapStateToProps = (state, ownProps) => {
  const { displayProperties, admin } = state;
  const { width } = displayProperties;
  const { djModeActive } = admin;
  const { variant } = ownProps;

  let swipeLeftPath = BROWSE_BY_TITLE_PATH;
  let swipeRightPath = SEARCH_PATH;

  if (variant === BROWSE_BY_TITLE) {
    swipeLeftPath = djModeActive ? ADMIN_PATH : SEARCH_PATH;
    swipeRightPath = BROWSE_BY_ARTIST_PATH;
  }

  return {
    width,
    swipeLeftPath,
    swipeRightPath
  };
};

const mapDispatchToProps = dispatch => ({
  setSelectedButton: string => dispatch(setSelectedButton(string)),
  search: (string, searchBy) => dispatch(searchForSongs(string, searchBy, true))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BrowseBy);
