import React from "react";
import { withRouter } from "react-router-dom";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  Grid,
  makeStyles,
  Typography,
  CircularProgress
} from "@material-ui/core";

import ReportSongIssue from '../ReportSongIssue/ReportSongIssueConnector';

import logo from "../../assets/images/logo-karaoke-png.png";

const useStyles = makeStyles(theme => ({
  dialogContainer: {
    height: "auto",
    overflowY: "unset"
  },
  heading: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between"
  },
  img: {
    height: "30px"
  }
}));

const SelectedSongDialog = props => {
  const {
    selectedSong,
    handleClose,
    sendingRequest,
    completeRequest,
    submitRequest,
    history
  } = props;
  const classes = useStyles();
  const {
    Title,
    Artist,
    DiscRef,
    Key,
    Length,
    CompletedDateTime,
    DateTime,
    Singer
  } = selectedSong || {};

  const [name, setName] = React.useState("");
  const [anchorEl, setAnchorEl] = React.useState(null);

  React.useEffect(() => {
    setName('');
    setAnchorEl(null);
  }, [selectedSong]);

  let dialogTitle = "Request Slip";
  let actionButton = (
    <Button
      onClick={() =>
        submitRequest(name, history)
      }
      disabled={name.length === 0 || sendingRequest}
    >
      {sendingRequest ? <CircularProgress size={24} /> : "Submit"}
    </Button>
  );

  if (sendingRequest) {
    dialogTitle = "Submitting Request!";
  } else if (DateTime && !CompletedDateTime) {
    actionButton = (
      <Button onClick={() => completeRequest(selectedSong)}>Completed</Button>
    );
    dialogTitle = `Requested: ${DateTime.toLocaleString().match(
      /[0-9]{1,2}:[0-9]{1,2}:[0-9]{1,2}/g
    )}`;
  } else if (CompletedDateTime) {
    actionButton = <Button onClick={e => setAnchorEl(e.currentTarget)} >Report Issue</Button>;
    dialogTitle = `Completed: ${CompletedDateTime.toLocaleString().match(
      /[0-9]{1,2}:[0-9]{1,2}:[0-9]{1,2}/g
    )}`;
  }

  const handleKeypress = e => {
    const { key, target } = e;
    const { value } = target;
    if (key === 'Enter') {
      submitRequest(name, history)
    }

    if (value.length <= 80) {
      setName(value)
    }
  }

  const closeDialog = () => {
    if (!sendingRequest) {
      handleClose();
      setAnchorEl(null)
    }
  }

  return (
    <Dialog
      open={Boolean(selectedSong)}
      onClose={closeDialog}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      fullWidth
      maxWidth="sm"
    >
      <DialogTitle id="alert-dialog-title">
        <div className={classes.heading}>
          <Typography variant="inherit">{dialogTitle}</Typography>
          <img className={classes.img} src={logo} alt="logo" />
        </div>
      </DialogTitle>
      <DialogContent classes={{ root: classes.dialogContainer }}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            {Singer ? (
              <TextField
                id="name"
                label="Singer"
                value={Singer}
                variant="outlined"
                fullWidth
                disabled
              />
            ) : (
                <TextField
                  id="name"
                  label="Enter Your Name"
                  value={name}
                  onChange={handleKeypress}
                  variant="outlined"
                  fullWidth
                  autoFocus
                  onKeyPress={handleKeypress}
                />
              )}
          </Grid>
          <Grid item xs={12}>
            <TextField
              id="title"
              label="Title"
              value={Title || ""}
              variant="outlined"
              fullWidth
              disabled
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              id="artist"
              label="Artist"
              value={Artist || ""}
              variant="outlined"
              fullWidth
              disabled
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              id="ref"
              label="Ref"
              value={DiscRef || ""}
              variant="outlined"
              fullWidth
              disabled
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              id="length"
              label="Length"
              value={Length || ""}
              variant="outlined"
              fullWidth
              disabled
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              id="key"
              label="Key"
              value={Key || ""}
              variant="outlined"
              fullWidth
              disabled
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        {actionButton}
        <Button onClick={handleClose} disabled={sendingRequest}>
          Close
        </Button>
      </DialogActions>
      <ReportSongIssue anchorEl={anchorEl} handleClose={() => setAnchorEl(null)} />
    </Dialog>
  );
};

export default withRouter(SelectedSongDialog);
