import { delay, call, take, put, race } from "redux-saga/effects";
import { getSubmittedRequests } from "../actions";
import {
  START_POLL_FOR_TICKER,
  STOP_POLL_FOR_TICKER
} from "../actionTypes/api";

function* pollForSubmittedRequests() {
  while (true) {
    try {
      yield put(getSubmittedRequests());
      yield delay(180000);
    } catch (err) {
      yield delay(10000);
    }
  }
}

function* watcherSaga() {
  while (true) {
    yield take(START_POLL_FOR_TICKER);
    yield race([call(pollForSubmittedRequests), take(STOP_POLL_FOR_TICKER)]);
  }
}

export default watcherSaga;
