/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";
import { withRouter } from "react-router-dom";
import {
  makeStyles,
  Typography,
  TextField,
  InputAdornment,
  IconButton,
  Button,
} from "@material-ui/core";
import { ToggleButton, ToggleButtonGroup } from "@material-ui/lab";
import { Clear, Search, FilterList, TouchApp } from "@material-ui/icons";
import { TOOLBAR_HEIGHT } from "../../constants/index";

import ResultsList from '../../components/ResultsList/ResultsList';
import NoResultsMessage from '../../components/NoResultsMessage/NoResultsMessage';
import GettingResultsSpinner from '../../components/GettingResultsSpinner/GettingResultsSpinner';

const useStyles = makeStyles(theme => ({
  results: {
    minHeight: `calc(100vh - ${TOOLBAR_HEIGHT}px)`
  },
  headerContainer: {
    position: "sticky",
    top: TOOLBAR_HEIGHT,
    width: "100%",
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    backgroundColor: theme.palette.background.paper,
    zIndex: 100,
  },
  titleContainer: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "baseline"
  },
  filterContainer: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    marginBottom: theme.spacing(1),
    marginTop: theme.spacing(2),
  },
  textField: {
    maxWidth: "50vw",
    marginRight: theme.spacing(3)
  },
  label: {
    display: "flex",
    alignItems: "center"
  },
  resultsList: {
    display: "flex",
    flexDirection: "column",
    width: "100%",
    height: "100%",
    minHeight: "100%",
    marginBottom: "20px"
  },
  buttonIcon: {
    marginRight: theme.spacing(1),
  },
  inline: {
    display: 'flex',
    flexDirections: 'row',
    alignItems: "center",
  },
  info: {
    width: '100%',
    display: 'flex',
    justifyContent: 'space-between',
  },
}));

const Results = props => {
  const {
    setSelectedButton,
    searchResults,
    gettingSearchResults,
    clearSearchResults,
    currentSearch,
    history
  } = props;
  const classes = useStyles();

  const [filter, setFilter] = React.useState("");
  const [filterBy, setFilterBy] = React.useState(0);
  const [filteredResults, setFilteredResults] = React.useState(searchResults);

  const searchResultsCount = searchResults.length;

  const filterResults = () => {
    const userInput = filter.toLowerCase();
    setFilteredResults(
      searchResults.filter(obj => {
        const { Artist, Title } = obj;
        const matchArtist = Artist.toLowerCase().includes(userInput);
        const matchTitle = Title.toLowerCase().includes(userInput);

        if (
          (filterBy === 0 && matchTitle) ||
          (filterBy === 1 && matchArtist) ||
          (filterBy === 2 && (matchArtist || matchTitle))
        ) {
          return obj;
        }

        return null;
      })
    );
  };

  React.useEffect(() => {
    setFilteredResults(searchResults);
  }, [searchResults]);

  React.useEffect(() => {
    setSelectedButton(null);

    return () => {
      clearSearchResults();
    }
  }, []);

  React.useEffect(() => {
    if (filter.length === 0) {
      setFilteredResults(searchResults);
    } else if (filter.length >= 1) {
      filterResults();
    }
  }, [filter, filterBy]);

  React.useEffect(() => {
    let redirect;
    if (!gettingSearchResults && searchResults.length === 0) {
      redirect = setTimeout(() => {
        history.push("/");
      }, [7000]);
    }

    if (searchResults.length > 0) {
      // cancel timeout when result returned after this has started
      clearTimeout(redirect);
    }

    return () => {
      clearTimeout(redirect);
    }
  }, [gettingSearchResults, searchResults]);

  const disableFilter = gettingSearchResults || searchResults.length === 0;

  const handleFilterByOnChange = value => {
    if (value === null) {
      return;
    }

    setFilterBy(value);
  };

  let pageContent;
  if (gettingSearchResults) {
    pageContent = <GettingResultsSpinner />;
  } else if (searchResults.length === 0) {
    pageContent = <NoResultsMessage />;
  } else {
    pageContent = <ResultsList resultsList={filteredResults} />
  }

  return (
    <div className={classes.results}>
      <div id="header-container" className={classes.headerContainer}>
        <div id="title-container" className={classes.titleContainer}>
          <Typography variant="h4" gutterBottom>
            Search Results
          </Typography>
          <Button variant="outlined" size="large" onClick={() => history.push('/search')}>
            <Search className={classes.buttonIcon} />
            New Search
          </Button>
        </div>
        <Typography gutterBottom >{currentSearch}</Typography>
        <div id="search=box" className={classes.filterContainer}>
          <TextField
            id="filter-text-field"
            label="Filter results"
            value={filter}
            onChange={e => setFilter(e.target.value)}
            className={classes.textField}
            variant="outlined"
            fullWidth
            disabled={disableFilter}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FilterList />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton disabled={!filter} onClick={() => setFilter("")}>
                    <Clear />
                  </IconButton>
                </InputAdornment>
              )
            }}
          />
          <ToggleButtonGroup
            size="large"
            value={filterBy}
            exclusive
            onChange={(event, value) => handleFilterByOnChange(value)}
          >
            <ToggleButton key="title" value={0} disabled={disableFilter}>
              <Typography>Title</Typography>
            </ToggleButton>
            <ToggleButton key="artist" value={1} disabled={disableFilter}>
              <Typography>Artist</Typography>
            </ToggleButton>
            <ToggleButton key="both" value={2} disabled={disableFilter}>
              <Typography>Both</Typography>
            </ToggleButton>
          </ToggleButtonGroup>
        </div>
        {searchResultsCount > 0 ? (
          <div className={classes.info}>
            <div className={classes.inline}>
              <TouchApp />
              <Typography >Select a song to submit a request</Typography>
            </div>
            <Typography>{`${filteredResults.length}/${searchResultsCount}`}</Typography>
          </div>
        ) : null}
      </div>
      <div id="results-list" className={classes.resultsList}>
        {pageContent}
      </div>
    </div>
  );
};

export default withRouter(Results);
