import React from 'react';
import { Menu, MenuItem } from '@material-ui/core';

const ReportSongIssue = props => {
  const { submitSongIssue, anchorEl, handleClose } = props;

  const list = [
    'Song does not match label',
    "No Audio",
    "No Words",
    "Corrupted / Unusable",
    "Song is incomplete",
    "Other"
  ];

  return (
    <Menu
      id="issue-menu"
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    >
      {list.map(e => <MenuItem key={e} onClick={() => submitSongIssue(e)}>{e}</MenuItem>)}
    </Menu>
  )
};

export default ReportSongIssue;