import { connect } from "react-redux";
import Results from "./Results";
import { setSelectedButton, clearSearchResults } from "../../store/actions/index";

const mapStateToProps = state => {
  const { results, api } = state;
  const { searchResults, currentSearch } = results;
  const { gettingSearchResults } = api;
  return {
    searchResults,
    gettingSearchResults,
    currentSearch,
  };
};

const mapDispatchToProps = dispatch => ({
  setSelectedButton: string => dispatch(setSelectedButton(string)),
  clearSearchResults: () => dispatch(clearSearchResults())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Results);
