import { connect } from "react-redux";
import ReportSongIssue from "./ReportSongIssue";
import { submitSongIssue } from "../../store/actions/index";

const mapDispatchToProps = dispatch => ({
  submitSongIssue: issue => dispatch(submitSongIssue(issue))
});

export default connect(null, mapDispatchToProps)(ReportSongIssue);