import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { GET_REQUESTS } from "../actionTypes/api";
import { getBaseUrl } from "./selectors";
import {
  setSongRequests,
  setCompletedSongRequests,
  setPollForRequestStatus
} from "../actions/index";

const sortArrayByDateTime = array => {
  return array.sort((a, b) => {
    if (a.DateTime < b.DateTime) {
      return -1;
    }

    if (a.DateTime > b.DateTime) {
      return 1;
    }

    return 0;
  });
};

const sortArrayByCompletedDateTime = array => {
  // reverse sort
  return array.sort((a, b) => {
    if (a.CompletedDateTime > b.CompletedDateTime) {
      return -1;
    }

    if (a.CompletedDateTime < b.CompletedDateTime) {
      return 1;
    }

    return 0;
  });
};

function* getRequests() {
  const baseUrl = yield select(getBaseUrl);
  yield put(setPollForRequestStatus('Updating...'));

  try {
    const response = yield call(axios.get, `${baseUrl}/submitted-requests`);
    const { data } = response;

    let pending = [];
    let completed = [];

    for (let i = 0; i < data.length; i += 1) {
      const request = data[i];
      const { State } = request;

      if (State === "completed") {
        completed.push(request);
      } else {
        pending.push(request);
      }
    }

    // sort the arrays
    pending = sortArrayByDateTime(pending);
    completed = sortArrayByCompletedDateTime(completed);

    // number the pending requests;
    pending = pending.map((obj, i) => ({
      ...obj,
      Position: i + 1,
    }));

    yield put(setSongRequests(pending));
    yield put(setCompletedSongRequests(completed));
    yield put(setPollForRequestStatus(`Updated: ${new Date().toLocaleTimeString()}`));
  } catch (error) {
    yield put(setPollForRequestStatus(`Update failed: ${error}`));
  }
}

function* watcherSaga() {
  yield takeLatest(GET_REQUESTS, getRequests);
}

export default watcherSaga;
