import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Container } from "@material-ui/core";
import Toolbar from "../../components/Toolbar/ToolbarConnector";
import { TOOLBAR_HEIGHT } from "../../constants/index";

import LogonDialog from "../../components/LogonDialog/LogonDialogConnector";
import SelectedSongDialog from "../../components/SelectedSongDialog/SelectedSongDialogConnector";
import Notification from "../../components/Notification/NotificationConnector";
import Ticker from "../../components/Ticker/TickerConnector";

const useStyles = makeStyles(theme => ({
  background: {
    minWidth: "100%",
    minHeight: "100%",
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.text.primary
  },
  container: {
    marginTop: TOOLBAR_HEIGHT,
    background: "inherit",
    minHeight: `calc(100vh - ${TOOLBAR_HEIGHT}px)`,
  }
}));

const Layout = props => {
  const { children } = props;
  const classes = useStyles();

  return (
    <div className={classes.background}>
      <Toolbar />
      <Container maxWidth="xl" id="container" className={classes.container}>
        {children}
      </Container>
      <SelectedSongDialog />
      <LogonDialog />
      <Notification />
      <Ticker />
    </div>
  );
};

export default Layout;
