import React from "react";
import { makeStyles, AppBar, IconButton } from "@material-ui/core";
import { Lock, LockOpen } from "@material-ui/icons";
import { TOOLBAR_HEIGHT } from "../../constants/index";
import ToolbarButton from "../ToolbarButton/ToolbarButtonConnector";

const useStyles = makeStyles(theme => ({
  appBar: {
    height: TOOLBAR_HEIGHT,
    display: "flex",
    flexDirection: "row",
    alignItems: "center"
  },
  toolbarContent: {
    height: "100%",
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexGrow: 1
  },
  iconButton: {
    height: 48,
    width: 48
  }
}));

const Toolbar = props => {
  const { toolbarOptions, djModeActive, handleLogonClick } = props;
  const classes = useStyles();

  return (
    <AppBar className={classes.appBar} position="fixed">
      <div className={classes.iconButton} />
      <div className={classes.toolbarContent}>
        {toolbarOptions.map(obj => (
          <ToolbarButton key={obj.buttonType} {...obj} />
        ))}
      </div>
      <IconButton className={classes.iconButton} onClick={handleLogonClick}>
        {djModeActive ? <LockOpen /> : <Lock />}
      </IconButton>
    </AppBar>
  );
};

export default Toolbar;
