import { compose, createStore, applyMiddleware } from "redux";
import createSagaMiddleware from "redux-saga";
import thunk from "redux-thunk";

import rootReducer from "./reducers/index";
import rootSaga from "./sagas/index";

const sagaMiddleware = createSagaMiddleware();
const middlewareCollection = [thunk, sagaMiddleware];

const enhancerCollection = [applyMiddleware(...middlewareCollection)];

const devTools =
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__();

if (process.env.ENV !== "prod") {
  // if this build is not in the production environment...

  // implement redux chrome devtools
  // https://chrome.google.com/webstore/detail/redux-devtools/lmhkpmbekcpmknklioeibfkpmmfibljd?hl
  if (devTools) {
    enhancerCollection.push(devTools);
  }
}

const store = createStore(rootReducer, compose(...enhancerCollection));
sagaMiddleware.run(rootSaga);

export default store;
