import React from 'react';
import InfiniteScroll from 'react-infinite-scroller';
import KaraokeSongSummaryListItem from '../KaraokeSongSummaryListItem/KaraokeSongSummaryListItemConnector';

const ResultsList = props => {
  const { resultsList } = props;
  const [renderList, setRenderList] = React.useState([]);

  React.useEffect(() => {
    setRenderList(resultsList.slice(0, renderList.length + 10));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [resultsList]);


  const loadMoreResults = () => {
    setRenderList(resultsList.slice(0, renderList.length + 10));
  }

  return (
    <InfiniteScroll
      pageStart={0}
      loadMore={loadMoreResults}
      hasMore={renderList.length !== resultsList.length}
      loader={<div className="loader" key={0}>Loading ...</div>}
    >
      {renderList.map(obj => (
        <KaraokeSongSummaryListItem key={obj.UID} songObj={obj} />
      ))}
    </InfiniteScroll>)
}

export default ResultsList