import { connect } from "react-redux";
import SelectedSongDialog from "./SelectedSongDialog";
import {
  clearSelectedSong,
  submitRequest,
  completeRequest
} from "../../store/actions/index";

const mapStateToProps = state => {
  const { results, api } = state;
  const { selectedSong } = results;
  const { sendingRequest } = api;
  return {
    selectedSong,
    sendingRequest
  };
};

const mapDispatchToProps = dispatch => ({
  handleClose: () => dispatch(clearSelectedSong()),
  submitRequest: (singer, onSuccessAction) =>
    dispatch(submitRequest(singer, onSuccessAction)),
  completeRequest: obj => dispatch(completeRequest(obj)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SelectedSongDialog);
