import React from "react";
import "./Ticker.css";

const Ticker = props => {
  const { tickerString, djModeActive } = props;
  if (djModeActive || tickerString.length === 0) {
    return null;
  }

  const scrollSpeed = Math.ceil(tickerString.length / 10) * 1.5;

  const style = {
    animationDuration: scrollSpeed + "s"
  };

  return (
    <div>
      <div>
        <p style={{
          position: 'fixed',
          left: 0,
          bottom: 0,
          height: '25px',
          margin: '0 auto',
          padding: '0 8px',
          backgroundColor: '#40a4c8',
          color: 'white',
          zIndex: 50,
          display: 'inline-block',
        }}>
          <span>Requested:</span>
        </p>
      </div>
      <div className="Ticker" >
        <p className="Marquee" style={style}>
          <span>{tickerString}</span>
        </p>
      </div>
      <div id="padding-right"
        style={{
          position: 'fixed',
          right: 0,
          bottom: 0,
          minHeight: '25px',
          minWidth: '8px',
          backgroundColor: '#40a4c8',
          zIndex: 50,
          display: 'inline-block',
        }} />
    </div>
  );
};

export default Ticker;
