import * as actionTypes from "../actionTypes/admin";

export const showLogonDialog = bool => ({
  type: actionTypes.SHOW_LOGON_DIALOG,
  bool
});

export const setDjModeActive = bool => ({
  type: actionTypes.SET_DJ_MODE_ACTIVE,
  bool
});

export const setBaseUrl = url => ({
  type: actionTypes.SET_BASE_URL,
  url
});

export const setNotificationMessage = string => ({
  type: actionTypes.SET_NOTIFICATION_MESSAGE,
  string
});

export const setNotificationOpen = bool => ({
  type: actionTypes.SET_NOTIFICATION_OPEN,
  bool
});

export const handleLogonRequest = credentials => ({
  type: actionTypes.HANDLE_LOGON_REQUEST,
  credentials
});

export const handleLogoffRequest = () => ({
  type: actionTypes.HANDLE_LOGOFF_REQUEST
});

export const setToken = token => ({
  type: actionTypes.SET_TOKEN,
  token
});
