import { connect } from 'react-redux';
import KaraokeSongSummaryListItem from './KaraokeSongSummaryListItem';
import { setSelectedSong, completeRequest } from '../../store/actions/index';

const mapDispatchToProps = dispatch => ({
  setSelectedSong: obj => dispatch(setSelectedSong(obj)),
  completeRequest: obj => dispatch(completeRequest(obj)),
});

export default connect(null, mapDispatchToProps)(KaraokeSongSummaryListItem);