import { connect } from "react-redux";
import LogonDialog from "./LogonDialog";
import {
  showLogonDialog,
  handleLogonRequest,
  handleLogoffRequest
} from "../../store/actions/index";
import sha256 from "./sha256";

const mapStateToProps = state => {
  const { admin } = state;
  const { logonDialogOpen, djModeActive } = admin;

  return {
    logonDialogOpen,
    djModeActive
  };
};

const mapDispatchToProps = dispatch => ({
  handleClose: () => dispatch(showLogonDialog(false)),
  handleSubmit: (djModeActive, password = null) => {
    if (djModeActive) {
      dispatch(handleLogoffRequest());
    } else {
      dispatch(handleLogonRequest(sha256(password)));
    }
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LogonDialog);
