import * as actionTypes from "../actionTypes/api";

export const startPollForRequests = () => ({
  type: actionTypes.START_POLL_FOR_REQUESTS
});

export const stopPollForRequests = () => ({
  type: actionTypes.STOP_POLL_FOR_REQUESTS
});

export const getRequests = () => ({
  type: actionTypes.GET_REQUESTS
});

export const startPollForTicker = () => ({
  type: actionTypes.START_POLL_FOR_TICKER
});

export const stopPollForTicker = () => ({
  type: actionTypes.STOP_POLL_FOR_TICKER
});

export const getSubmittedRequests = () => ({
  type: actionTypes.GET_SUBMITTED_REQUESTS
});

export const submitRequest = (singer, history) => ({
  type: actionTypes.SUBMIT_REQUEST,
  singer,
  history
});

export const setSendingRequest = bool => ({
  type: actionTypes.SET_SENDING_REQUEST,
  bool
});

export const searchForSongs = (string, searchBy, browse) => ({
  type: actionTypes.SEARCH_FOR_SONGS,
  string,
  searchBy,
  browse
});

export const setGettingSearchResults = bool => ({
  type: actionTypes.SET_GETTING_SEARCH_RESULTS,
  bool
});

export const setPollForRequestStatus = string => ({
  type: actionTypes.SET_POLL_FOR_REQUEST_STATUS,
  string,
});

export const completeRequest = songObj => ({
  type: actionTypes.COMPLETE_REQUEST,
  songObj,
});

export const clearRequests = () => ({
  type: actionTypes.CLEAR_REQUESTS,
});

export const reloadDatabase = () => ({
  type: actionTypes.RELOAD_DATABASE,
});

export const submitSongIssue = issue => ({
  type: actionTypes.SUBMIT_SONG_ISSUE,
  issue,
});
